import React, {Component} from 'react';
import Button from '../misc/Button.js';
import Event from './Event.js';
import EventObject from './EventObject.js';
import '../../styles/event.css';

class EventList extends Component {

	constructor (props) {
		super(props);
		this.state = { events: [] };
		this.displayLimit = 4;
		this.getEvents().then((eventList) => {
			this.setState({ events: eventList });
		});
	}

	// Get events stored in a janky text file because I didn't feel like getting Calendar API to work
	async getEvents () {
		const newEvents = []
		const response = await fetch("./events.txt");
		const eventText = await response.text();
		this.parseEventText(eventText, newEvents);
		return newEvents
	}

	parseEventText (text, list) {
		const lines = text.split("\n");
		for (let i = 1; i < lines.length; i++) {
			const line = lines[i];
			if (line.trim().length > 0) {
				const temp = line.split("; ");
				console.log(temp);
				const obj = new EventObject(temp[0], temp[1], temp[2], temp[3], temp[4]);
				console.log(obj);
				list.push(obj);
			}
		}
	}

	render () {
		const events = (this.state.events.length > 0) ? this.state.events.map((event, i) => {
			return <Event title={event.title} date={event.date} start={event.start} end={event.end} link={event.link} key={i} />
		}) : <p>No events to be found.</p>;
		return (
			<div className="schedule">
				<h3 className="--normal">Upcoming Events</h3>
				<div className="events">
					{events}
				</div>
				<Button link="#" style="normal" text="Subscribe to our Calendar." />
			</div>
		);
	}

}

export default EventList;